.pane-user-login {
  display: block;
  width: 100%;
  order: 4;
  background: #f0f0f0;
}

.pane-user-login select {
  background: white;
  border: 1px solid #d5d5d5;
  height: 41px;
  width: 100%;
  font-size: 13px;
  padding: 10px 30px;
  color: #969696;
  text-transform: uppercase;
  font-weight: normal;
  letter-spacing: 1px;
}

.pane-user-login input[type="text"],
.pane-user-login input[type="email"],
.pane-user-login input[type="submit"],
.pane-user-login input[type="password"] {
  text-decoration: none;
  border-radius: 6px;
  min-width: 250px;
  display: inline-block;
  position: relative;
  height: 41px;
  border: 1px solid #d5d5d5;
  color: black;
  font-size: 15.4px;
  font-family: "SourceSansPro", sans-serif;
  width: 100%;
  box-sizing: border-box;
  padding: 1rem;
  appearance: none;
}

.pane-user-login input[type="submit"] {
  line-height: 1em;
  padding: 15px 22px;
  margin-top: 1rem;
  border-radius: 0;
  background-color: #dcdcdc;
  width: 100%;
}

.pane-user-login input[type="submit"]:hover {
  background-color: #e6e6e6;
  cursor: pointer;
}

.pane-user-login label {
  font-size: 16px;
  line-height: 22px;
  font-family: "SourceSansProLight", sans-serif;
  color: #505050;
  margin: 0 0 5px;
  display: block;
}

.pane-user-login .form-item {
  position: relative;
  margin-bottom: 1rem;
}

.pane-user-login form .description {
  color: #047537;
  font-family: "SourceSansPro", sans-serif;
  margin-top: 5px;
}

.pane-user-login form#user-login-form {
  max-width: 500px;
  margin: 0 auto;
  padding: 2rem;
}

