.pane-search-form {
  height: 6.875rem;
  margin-left: auto;
}

@media all and (min-width: 49rem) {
  .pane-search-form {
    height: auto;
    width: 100%;
  }
}

.search-form {
  border-top: 1px solid #d8d8d8;
  font-family: "SourceSansPro", sans-serif;
}

.search-form__icon__wrapper {
  align-items: center;
  cursor: pointer;
  display: flex;
  height: 6.875rem;
  justify-content: center;
}

.search-form__icon__wrapper::before {
  font-family: "icomoon";
  speak: none;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  content: '';
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  color: #9c9c9c;
  content: "";
  font-size: 2.5rem;
  position: absolute;
  z-index: 99999;
}

@media all and (min-width: 49rem) {
  .search-form__icon__wrapper::before {
    color: #ffffff;
    font-size: 1.5rem;
    position: relative;
    top: 5px;
  }
}

@media all and (min-width: 49rem) {
  .search-form__icon__wrapper.open {
    height: 2rem;
    left: calc(50% - 1rem);
    position: relative;
    width: 2rem;
  }
}

.search-form__icon__wrapper.open::before {
  content: '\ea0f';
  font-size: 1.5rem;
}

@media all and (min-width: 49rem) {
  .search-form__icon__wrapper.open::before {
    font-size: 1rem;
    top: 8px;
  }
}

@media all and (min-width: 49rem) {
  .search-form__icon__wrapper {
    background: #b81b40;
    display: block;
    height: 2rem;
    text-align: center;
  }
}

.search-form__icon {
  width: 4rem;
}

.search-form__content {
  background: #ffffff;
  display: none;
  height: 6.875rem;
  left: 0;
  max-width: 100%;
  position: absolute;
  top: 0;
  width: 100%;
  z-index: 4;
}

@media all and (min-width: 49rem) {
  .search-form__content {
    margin: 0 auto;
    position: relative;
    width: 1200px;
  }
}

.search-form__input--text {
  border: 0;
  box-sizing: border-box;
  color: gray;
  display: block;
  font-family: "SourceSansProLight", sans-serif;
  font-size: 2rem;
  height: 6.875rem;
  padding: 0 1.875rem;
  width: 100%;
}

.search-form__icon--close {
  background: #b81b40;
  height: 2rem;
  position: absolute;
  right: 1rem;
  width: 2rem;
}

@media all and (min-width: 49rem) {
  .search-form__icon--close {
    right: auto;
    left: calc(50% - 1rem);
  }
}

.search-form__icon--close::before {
  font-family: "icomoon";
  speak: none;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  content: '';
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  color: #ffffff;
  content: "";
  font-size: 1rem;
  left: .5rem;
  position: absolute;
  top: .5rem;
}

html.js input.form-autocomplete {
  background-image: none;
}

.site-header {
  background: white;
}

.pane-menu-block-main-menu {
  width: 100%;
