  display: flex;
}

.menu-name-main-menu li a {
  color: white;
  text-decoration: none;
  font-family: "SourceSansPro", sans-serif;
  font-weight: bold;
  font-size: 1rem;
}

@media all and (min-width: 49rem) {
  .menu-name-main-menu li a {
    color: #505050;
    font-weight: 700;
    line-height: 5rem;
    padding: 2.5rem;
  }
  .menu-name-main-menu li a:hover {
    background: #f0f0f0;
  }
}

@media all and (min-width: 49rem) {
  .menu-name-main-menu li {
    height: 100%;
    display: inline-block;
  }
}

@media all and (min-width: 49rem) {
